/*eslint-disable @typescript-eslint/no-unused-vars */
import { HUtils } from "@macashipo/mlib"
import React, { useState } from "react"
import { fnListOfFormControl } from "./fnListOfFormControl"
import { fnListMyForm, kresetFormControl } from "src/helpers/fnList"
import useForceUpdate from "./useForceUpdate"
import {
  FlowFnListFromExtControl,
  FlowMergeWithCustomProps,
} from "src/helpers/Flows/FormControl"

// var countForceUpdate = 0
// const countForceUpdateMax = 200
function useConfigFormControl({ props } = {}) {
  // countForceUpdate++
  const refConfigFormControl = React.useRef(props.configFormControl)
  const configFormControl = refConfigFormControl.current || {}
  const { label, fieldName } = configFormControl
  FlowMergeWithCustomProps({ configFormControl, custom: props.custom })
  const more = configFormControl.more || {}
  // const [valueFU, setValueFU] = useState(0) // integer state
  const defaultValues = fnListMyForm.getDefaultValues(props.fnList) || {}
  // console.warn("defaultValues", defaultValues)
  const [valueControl, setValueControl] = useState(
    defaultValues[fieldName] || ""
  )
  const refFnList = React.useRef(fnListOfFormControl.init({ props }))
  const fnList = refFnList.current

  const forceUpdate = useForceUpdate()
  // const forceUpdate = () => {
  //   console.warn("forceUpdate")
  //   if (countForceUpdate < countForceUpdateMax) setValueFU(valueFU + 1)
  // }

  fnListMyForm.addFnListFromControl(fnList, [
    kresetFormControl,
    () => {
      console.warn("reset form control")
      setValueControl(defaultValues[fieldName] || "")
    },
    fieldName,
  ])
  fnListMyForm.addFnListFromControl(fnList, [
    FlowFnListFromExtControl.keys.forceUpdate,
    () => {
      forceUpdate()
    },
    fieldName,
  ])

  return {
    more: more,
    forceUpdate,
    configFormControl,
    label,
    defaultValues,
    fieldName,
    optionKey: more.optionKey,
    valueControl,
    fnList,
    setValueControl,
    updateValueControl: v => {
      // console.warn("update value", props)
      setValueControl(v)
      HUtils.runFuntion(fnList, "onUpdateValue", [
        {
          fieldName: fieldName,
          newValue: v,
        },
      ])
    },
    updateValueControlWithMoreFields: (v, moreValues = {}) => {
      setValueControl(v)
      HUtils.runFuntion(fnList, "onUpdateValue", [
        {
          fieldName: fieldName,
          newValue: v,
        },
        ...Object.keys(moreValues).map((f, i) => {
          return {
            fieldName: f,
            newValue: moreValues[f],
          }
        }),
      ])
    },
    getDisabled: () => {
      return more.disabled === true
    },
    getMoreAttrs: () => {
      let _moreAttrs = {}
      // console.log("more:", more)
      if (more.autoComplete != null) {
        _moreAttrs.autoComplete = String(more.autoComplete)
        if (more.autoComplete === false) {
          _moreAttrs.autoComplete = "new-password" //for chrome disabled autofill
        }
      }
      if (more.placeholder) {
        _moreAttrs.placeHolder = more.placeholder
      }
      if (more.disabled) {
        _moreAttrs.disabled = more.disabled
      }
      if (more.rows) {
        _moreAttrs.rows = more.rows
      }
      return _moreAttrs
    },
  }
}

export default useConfigFormControl
