import React from "react"
import { MyBase, HFormat } from "@macashipo/mlib"
import WrapControl from "./_wrapControl"
import useConfigFormControl from "../../hooks/useConfigFormControl"
import styled from "styled-components"
const Wrap = styled.div`
  display: flex;
  position: relative;
  height: 40px;
  & > textarea {
    outline: none;
    resize: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    min-height: 100%;
    height: 100%;
    text-align: left;
    background: transparent;
  }
`
export default function MFCNumberDecimal(props) {
  const cfc = useConfigFormControl({ props })
  // console.warn("render MFCNumberDecimal:", props)
  return (
    <WrapControl cfc={cfc} {...props}>
      <Wrap className="mfc-number_decimal">
        <MyBase
          type="numeral"
          className="form-control"
          value={cfc.valueControl}
          onChange={v => {
            cfc.setValueControl(v)
          }}
          onBlur={() => {
            cfc.updateValueControl(cfc.valueControl)
          }}
          fnList={{
            getTypeInput: () => {
              return "textarea"
            },
            fnFormat: () => {
              return v => {
                return HFormat.format(v, { type: "decimal" })
              }
            },
          }}
        />
      </Wrap>
    </WrapControl>
  )
}
