import React from "react"
import styled from "styled-components"
import { MyLayout, MyLayoutHelpers, MyUI } from "@macashipo/mlib"
import { fnListMyFormControl } from "src/helpers/fnList"
import {
  FlowGetCanShow,
  FlowGetMoreOfKey,
  KMore,
} from "src/helpers/Flows/FormControl"
const Wrap = styled.div`
  margin-bottom: 0.5rem;
  & .alert {
    padding: 0.5rem;
    margin-bottom: 0.5rem;
  }
`
export default function MFCWrapControl(props) {
  const { cfc, noLabel } = props
  //check can show
  let _canShow = FlowGetCanShow({
    configFormControl: cfc.configFormControl,
    fnList: cfc.fnList,
    more: cfc.more,
  })
  if (_canShow === false) {
    if (cfc.more && cfc.more[KMore.htmlWhenHide]) {
      return <MyUI type="html" html={cfc.more[KMore.htmlWhenHide]} />
    }
    return null
  }

  let _typeWrapControl = fnListMyFormControl.fnGetTypeWrapControl(cfc.fnList)
  let _configWrapControl =
    FlowGetMoreOfKey({
      configFormControl: cfc.configFormControl,
      key: KMore.configWrapControl,
    }) || {}
  // console.log("_typeWrapControl:", _typeWrapControl)
  if (MyLayoutHelpers.Types[_typeWrapControl]) {
    return (
      <MyLayout
        type={_typeWrapControl}
        cfc={cfc}
        noLabel={noLabel}
        children={props.children}
        configWrapControl={_configWrapControl}
      />
    )
  }
  let _styleWrapControl =
    FlowGetMoreOfKey({
      configFormControl: cfc.configFormControl,
      key: KMore.styleWrapControl,
      defaultValue: false,
    }) || {}
  // console.warn("render wrap:", props, _styleWrapControl)
  return (
    <Wrap
      className="mfcw-v1"
      style={{ ..._styleWrapControl, ..._configWrapControl.style }}
    >
      {noLabel !== true && (
        <div style={_configWrapControl.styleLabel}>{cfc.label}</div>
      )}
      <div style={_configWrapControl.styleControl}>{props.children}</div>
    </Wrap>
  )
}
