import { fnListMyForm, kfnGetTypeWrapControl } from "src/helpers/fnList"
import { HUtils, HIs } from "@macashipo/mlib"
import * as AHOptions from "src/core/helpers/options"
import { KMore } from "src/helpers/Flows/FormControl"
const kgetSourceData = "getSourceData"
const fnListOfFormControl = {
  init({ props }) {
    const configFormControl = props.configFormControl || {}
    const { fieldName, more } = configFormControl
    let _fnList = {
      ...props.fnList,
    }
    // const more = oneTableCell.more
    // _fnList[kfnGetApiPath] = () => {
    //   return HUtils.runFuntion(oneTableCell.fnList, kfnGetApiPath, [])
    // }

    _fnList[kgetSourceData] = () => {
      console.info(kgetSourceData, fieldName)
      if (more && more.optionKey) {
        const sourceList = fnListMyForm.getSourceList(_fnList)
        if (sourceList && sourceList[more.optionKey]) {
          return sourceList[more.optionKey]
        } else {
          if (
            more.optionKey.indexOf(".") > -1 &&
            more.optionKey.indexOf("[") > -1
          ) {
            //option multi
            //
            let _dataForm = fnListMyForm.getCurrentValues(_fnList)
            return AHOptions.getOptionsByLevel(
              sourceList,
              more.optionKey,
              _dataForm
            )
          }
        }
      }
      return []
    }
    _fnList[kfnGetTypeWrapControl] = () => {
      let _typeWrapControlFromMyForm =
        fnListMyForm.fnGetTypeWrapControl(props.fnList) || ""
      if (more && more.hasOwnProperty(KMore.typeWrapControl)) {
        _typeWrapControlFromMyForm = more[KMore.typeWrapControl]
      }
      return _typeWrapControlFromMyForm
    }
    return _fnList
  },
  getSourceData() {},
}

const helpersOfFormControl = {}
export { helpersOfFormControl, fnListOfFormControl }
